@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{

    /* Colors */

    

    /* bright */
    --white: #ffffff;
    --sea-salt-white: #F6F7F8;
    --alice-white: #EBF2FA;
    --anti-flash-white: #EFF1F3;


    /* green */
    --dark-slate-grey: #204E4A;
    --dakr-spring-green: #297045;
    --forest-green: #2E933C;
    --mantis-green: #81C14B;

    /* orange */
    --buff: #ECBA82;

    /* blue */
    --marine-blue: #005EFA;
    --palentine-blue: #473BF0;
    --celestial-blue: #20A4F3;

    /* dark */
    --middark: #212A41;
    --dark: #1A1D25;
    --rich-dark: #011627;



    /* Color Variables*/
    --background1: var(--middark);
    --background2: var(--dark);
    --foreground: var(--sea-salt-white);
    --highlight: var(--marine-blue);
    


    /*<-- GLOBAL SCOPE -->*/
    
    /* Width */
    --size-relation: 78.5%;
    --size-relation-invert: calc(100% - var(--size-relation) - 4 * var(--holder-gap));

    --body-size: calc(100% - var(--navbar-size));


    /* Height */
    --header-size: 10%;
    --main-size: 70%;
    --footer-size: 18%;

    --corner-radius: 15px;


    /* Scrollbar */
    --scrollbar-size: 20px;

    /* NavBar */

    --navbar-size: 4.5%;
    --navbar-size-extended: 17.5%;
    --navbar-size-min: 64px;
    --navbar-time: 0.27s;
    --navbar-border: solid var(--background2) var(--holder-gap);
    --navbar-padding: 1.75rem;
    --navbar-transition: all linear var(--navbar-time);
    --navbar-button-border-radius: 8px;
    --navbar-button-height-small: 2.7rem;
    --navbar-button-height-large: 2.7rem;
    --navbar-gap: calc(2.7rem / 1.61);
    --nabbar-button-text-offset: -16rem;
    --navbar-button-icon-offset: 0%;


    /* DropSection */



    /* MODUL */
    --modul-padding: 0.86rem;

    --modul-max-width: 32rem;
    --modul-max-height: var(--modul-max-width) / 1.61;

    --modul-text-big: 1.1rem;
    --modul-text-medium: 0.92rem;
    --modul-text-small: 0.80rem;

    --modul-margin-small: 0.46rem;
    --modul-marign-medium: 0.56rem;
    --modul-margin-big: 0.66rem;

    /* Holder */
    --holder-min-width: 10rem;
    --holder-min-height: var(--holder-min-width);
    --holder-padding: 1rem;
    --holder-gap: 0.25rem;
    --holder-border: solid var(--holder-gap) var(--background2);


    /* Status Text */
    --statusText-size: 2.17rem;

    /* LP Text */
    --lpText-size: 1.37rem;


    background-color: #1A1D25;


    background-color: var(--background2);
}

*{
    font-family: 'Poppins', sans-serif;
}

/* App Wrapper */
.wrapper{

    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: #1A1D25;
    background-color: var(--background2);
    grid-column-gap: 0.25rem;
    -webkit-column-gap: 0.25rem;
            column-gap: 0.25rem;
    grid-column-gap: var(--holder-gap);
    -webkit-column-gap: var(--holder-gap);
            column-gap: var(--holder-gap);
}




/* Navbar  v0.2 */
.navBar{

    position: relative;
    display: flex;
    flex-direction: column;
    z-index: 9;
    transition: all linear 0.27s;
    transition: var(--navbar-transition);
    background-color: #212A41;
    background-color: var(--background1);
    width: 4.5%;
    width: var(--navbar-size);
    justify-content: center;
    align-items: center;
    grid-gap: calc(2.7rem / 1.61);
    gap: calc(2.7rem / 1.61);
    grid-gap: var(--navbar-gap);
    gap: var(--navbar-gap);
}
/* Mouse Hover NavBar*/
.navBar:hover{

    width: 17.5%;

    width: var(--navbar-size-extended);
    border-right: solid #1A1D25 0.25rem;
    border-right: var(--navbar-border);

}
.navBar:not(:hover){
    -webkit-animation: showBorder ease-in calc(2* 0.27s);
            animation: showBorder ease-in calc(2* 0.27s);
    -webkit-animation: showBorder ease-in calc(2* var(--navbar-time));
            animation: showBorder ease-in calc(2* var(--navbar-time));
}
@-webkit-keyframes showBorder{

    from{
        border-right: solid #1A1D25 0.25rem;
        border-right: solid var(--background2) var(--holder-gap);
    }
    to{
        border-right: 0;
    }
}
@keyframes showBorder{

    from{
        border-right: solid #1A1D25 0.25rem;
        border-right: solid var(--background2) var(--holder-gap);
    }
    to{
        border-right: 0;
    }
}
.navBar:hover .navButton-wrapper{

    -webkit-animation: linear extendNavButton 0.27s;

            animation: linear extendNavButton 0.27s;

    -webkit-animation: linear extendNavButton var(--navbar-time);

            animation: linear extendNavButton var(--navbar-time);
    width: 87.3%;
    height: 2.7rem;
    height: var(--navbar-button-height-large);
}
@-webkit-keyframes extendNavButton {
    
    from{
        
    }
    to{
        width: 87.3%;
        height: 2.7rem;
        height: var(--navbar-button-height-large);
    }
}
@keyframes extendNavButton {
    
    from{
        
    }
    to{
        width: 87.3%;
        height: 2.7rem;
        height: var(--navbar-button-height-large);
    }
}
.navBar:not(:hover) .navButton-wrapper{

    -webkit-animation: linear inflate 0.27s;

            animation: linear inflate 0.27s;

    -webkit-animation: linear inflate var(--navbar-time);

            animation: linear inflate var(--navbar-time);
}
@-webkit-keyframes inflate{

    from{
        width: 87.3%;
        height: 2.7rem;
        height: var(--navbar-button-height-large);
    }
    to{
        width: 2.7rem;
        width: var(--navbar-button-height-small);
        height: 2.7rem;
        height: var(--navbar-button-height-small);
    }
}
@keyframes inflate{

    from{
        width: 87.3%;
        height: 2.7rem;
        height: var(--navbar-button-height-large);
    }
    to{
        width: 2.7rem;
        width: var(--navbar-button-height-small);
        height: 2.7rem;
        height: var(--navbar-button-height-small);
    }
}
.navButton-wrapper{

    display: flex;
    position: relative;
    background-color: #1A1D25;
    background-color: var(--background2);
    width: 2.7rem;
    width: var(--navbar-button-height-small);
    height: 2.7rem;
    height: var(--navbar-button-height-small);
    margin: 0;
    border-radius: 8px;
    border-radius: var(--navbar-button-border-radius);
}
.navButton-wrapper:hover{

    background-color: #005EFA;

    background-color: var(--highlight);
}
/* Icon */
.navButton-wrapper .icon{


    transition: all linear 0.27s;


    transition: var(--navbar-transition);
    color: #F6F7F8;
    color: var(--foreground);
    margin: auto;
    
    
    position: relative;
    left: 0%;
}
.navBar:hover .navButton-wrapper .icon{

    -webkit-animation: moveIcon linear 0.27s;

            animation: moveIcon linear 0.27s;

    -webkit-animation: moveIcon linear var(--navbar-time);

            animation: moveIcon linear var(--navbar-time);
    left: 0%;
    left: var(--navbar-button-icon-offset);
    margin-left: calc(0.6rem * 1.61);
    margin-right: 0.6rem;
}
@-webkit-keyframes moveIcon {
    
    from{
        left: 0%;
    }
    to{
        left: 0%;
        left: var(--navbar-button-icon-offset);
    }
}
@keyframes moveIcon {
    
    from{
        left: 0%;
    }
    to{
        left: 0%;
        left: var(--navbar-button-icon-offset);
    }
}
.navBar:not(:hover) .navButton-wrapper .icon{

    -webkit-animation: moveIconBack linear 0.27s;

            animation: moveIconBack linear 0.27s;

    -webkit-animation: moveIconBack linear var(--navbar-time);

            animation: moveIconBack linear var(--navbar-time);
    left: 0%;
}
@-webkit-keyframes moveIconBack{

    from{
        left: 0%;
        left: var(--navbar-button-icon-offset);
    }
    to{
        left: 0%;
    }
}
@keyframes moveIconBack{

    from{
        left: 0%;
        left: var(--navbar-button-icon-offset);
    }
    to{
        left: 0%;
    }
}
/* Text Wrapper to hide overflow*/
.textwrapper{

    display: flex;
    position: relative;
    margin: auto;
    overflow: hidden;
    margin-left: 0;
    margin-right: 0;
    width: 0;
}
.navBar:hover .textwrapper{

    width: unset;
    margin-right: auto;
}
/* Text itself*/
.navButton-wrapper .text{

    
    position: relative;
    left: -16rem;
    left: var(--nabbar-button-text-offset);
    color: #F6F7F8;
    color: var(--foreground);
    -webkit-user-select: none;
            user-select: none;
}
.navBar:hover .navButton-wrapper .text{

    -webkit-animation: flyingtext linear calc(0.27s * 1);

            animation: flyingtext linear calc(0.27s * 1);

    -webkit-animation: flyingtext linear calc(var(--navbar-time) * 1);

            animation: flyingtext linear calc(var(--navbar-time) * 1);
    left: 0%;
}
@-webkit-keyframes flyingtext {
    
    from{
        left: -16rem;
        left: var(--nabbar-button-text-offset);
    }
    to{
        left: 0%;
    }
}
@keyframes flyingtext {
    
    from{
        left: -16rem;
        left: var(--nabbar-button-text-offset);
    }
    to{
        left: 0%;
    }
}
.navBar:not(:hover) .navButton-wrapper .text{

    -webkit-animation: outflyingtext linear 0.27s;

            animation: outflyingtext linear 0.27s;

    -webkit-animation: outflyingtext linear var(--navbar-time);

            animation: outflyingtext linear var(--navbar-time);
    left: -16rem;
    left: var(--nabbar-button-text-offset);
}
@-webkit-keyframes outflyingtext {
    
    from{
        left: 0%;
    }
    to{
        left: -16rem;
        left: var(--nabbar-button-text-offset);
    }
}
@keyframes outflyingtext {
    
    from{
        left: 0%;
    }
    to{
        left: -16rem;
        left: var(--nabbar-button-text-offset);
    }
}











/* Body */
.bodyWrapper{

    position: absolute;
    height: 100%;
    left: calc(4.5% + 0.25rem * 2);
    left: calc(var(--navbar-size) + var(--holder-gap) * 2);
    width: calc(calc(100% - 4.5%) - 0.25rem * 2);
    width: calc(var(--body-size) - var(--holder-gap) * 2);
}

.body{

    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    grid-row-gap: 0.25rem;
    row-gap: 0.25rem;
    grid-row-gap: var(--holder-gap);
    row-gap: var(--holder-gap);
}



.header{

    display: flex;
    min-height: 10%;
    min-height: var(--header-size);
    position: relative;
    width: 100%;
    grid-gap: 0.25rem;
    gap: 0.25rem;
    grid-gap: var(--holder-gap);
    gap: var(--holder-gap);
}


.main{

    display: flex;
    grid-column-gap: 0.25rem;
    -webkit-column-gap: 0.25rem;
            column-gap: 0.25rem;
    grid-column-gap: var(--holder-gap);
    -webkit-column-gap: var(--holder-gap);
            column-gap: var(--holder-gap);
    min-height: 70%;
    min-height: var(--main-size);
}

.footer{

    display: flex;
    grid-column-gap: 0.25rem;
    -webkit-column-gap: 0.25rem;
            column-gap: 0.25rem;
    grid-column-gap: var(--holder-gap);
    -webkit-column-gap: var(--holder-gap);
            column-gap: var(--holder-gap);
    min-height: 18%;
    min-height: var(--footer-size);
}



/* Header */
.mainHeader{

    position: relative;
    width: 78.5%;
    width: var(--size-relation);
    background-color: #212A41;
    background-color: var(--background1);
    display: flex;
    align-items: center;
}
.sideHeader{

    position: relative;
    width: calc(calc(100% - 78.5% - 4 * 0.25rem) + 0.25rem);
    width: calc(var(--size-relation-invert) + var(--holder-gap));
    background-color: #212A41;
    background-color: var(--background1);
    
}
/* Dropdown Menu */

/* normal */
.dropDownWrapper.override{

    background-color: #1A1D25;

    background-color: var(--background2);
    border-color: #1A1D25;
    border-color: var(--background2);
    margin-left: 2rem;
    color: #F6F7F8;
    color: var(--foreground);
}

/* hover */
.dropDownWrapper.override:hover{

    background-color: #005EFA;

    background-color: var(--highlight);
    border-color: #005EFA;
    border-color: var(--highlight);
    
}

/* normal hover active*/
.dropDownWrapper.override:hover:active{

    background-color: #005EFA;

    background-color: var(--highlight);
}

/* button when opened */
.btn.show.override{
    background-color: #005EFA;
    background-color: var(--highlight);
    border-color: #005EFA;
    border-color: var(--highlight);
}

/* button when opend and clicked*/
.btn.show.override:active{

    background-color: #005EFA;

    background-color: var(--highlight);
}

/* box */
.dropdown-menu.show {
    
    background-color: #1A1D25;
    
    background-color: var(--background2);
    color: #F6F7F8;
    color: var(--foreground);
}

/* list item */
button.dropdown-item{

    background-color: inherit;
    color: #F6F7F8;
    color: var(--foreground);
}

/* list item hover */
button.dropdown-item:hover{

    background-color: #005EFA;

    background-color: var(--highlight);
    color: inherit;
}





/* Main */
.dropSection{

    display: flex;
    flex-direction: column;
    width: 78.5%;
    width: var(--size-relation);
    min-height: 100%;
    grid-gap: 0.25rem;
    gap: 0.25rem;
    grid-gap: var(--holder-gap);
    gap: var(--holder-gap);
}

.dropTable{

    display: flex;
    overflow: scroll;
    background-color: #1A1D25;
    background-color: var(--background2);
    min-height: 100%;
    grid-column-gap: 0.25rem;
    -webkit-column-gap: 0.25rem;
            column-gap: 0.25rem;
    grid-column-gap: var(--holder-gap);
    -webkit-column-gap: var(--holder-gap);
            column-gap: var(--holder-gap);
}

.modulScroller{

    display: flex;
    flex-direction: column;
    overflow: scroll;
    grid-row-gap: 0.25rem;
    row-gap: 0.25rem;
    grid-row-gap: var(--holder-gap);
    row-gap: var(--holder-gap);
    width: calc(calc(100% - 78.5% - 4 * 0.25rem) + 0.25rem * 1);
    width: calc(var(--size-relation-invert) + var(--holder-gap) * 1);

}

.semesterHeader{

    background-color: #212A41;

    background-color: var(--background1);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 10rem;
    width: var(--holder-min-width);
    border-bottom: solid 0.25rem #1A1D25;
    border-bottom: var(--holder-border);
}

.semesterHeaderText{

    color: #F6F7F8;

    color: var(--foreground);
    text-align: center;
}

/* Semester div holds the Holders */
.semester{

    display: flex;
    flex-direction: column;
}

.semesterFooter{

    background-color: #212A41;

    background-color: var(--background1);
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    min-width: 10rem;
    min-width: var(--holder-min-width);
    min-height: calc(10rem / 1.61 / 2);
    min-height: calc(var(--holder-min-height) / 1.61 / 2);
    border-top: solid 0.25rem #1A1D25;
    border-top: var(--holder-border);
}

.footerLp{

    color: #F6F7F8;

    color: var(--foreground);
}


/* Holder without Modul */
.holder{

    width: 10rem;

    width: var(--holder-min-width);
    min-height: 10rem;
    min-height: var(--holder-min-height);
    background-color: #212A41;
    background-color: var(--background1);
    border-bottom: solid 0.25rem #1A1D25;
    border-bottom: var(--holder-border);
}
.holder.last{

    border-bottom: 0;
}

/* Modul in Scroller */
.modul{
    background-color: #212A41;
    background-color: var(--background1);
    color: #F6F7F8;
    color: var(--foreground);
    padding: 0.86rem;
    padding: var(--modul-padding);
}
.modul:hover{
    background-color: #005EFA;
    background-color: var(--highlight);
}
.name{

    font-size: 1.1rem;

    font-size: var(--modul-text-big);
}
.id{

    margin-bottom: 0.46rem;

    margin-bottom: var(--modul-margin-small);
    font-size: 0.92rem;
    font-size: var(--modul-text-medium);
}
.lp{

    margin-bottom: 0.46rem;

    margin-bottom: var(--modul-margin-small);
    font-size: 0.92rem;
    font-size: var(--modul-text-medium);
}
.angebot{

    margin-bottom: 0.56rem;

    margin-bottom: var(--modul-marign-medium);
    font-size: 0.80rem;
    font-size: var(--modul-text-small);
}
/* Modul Button Wrapper */
.modulButtons{

    display: flex;
    grid-gap: calc(1.61 * 0.25rem);
    gap: calc(1.61 * 0.25rem);
    grid-gap: calc(1.61 * var(--holder-gap));
    gap: calc(1.61 * var(--holder-gap));
}
/* Info Button */
.infoButton{

    
}
.infoButton:hover{

    background-color: #005EFA;

    background-color: var(--highlight);
}

/* Info Icon*/
.infoButton .icon{

    color: #F6F7F8;

    color: var(--foreground);
}

.doneButton{


}
.doneButton:hover{
    
    background-color: #005EFA;
    
    background-color: var(--highlight);
}
.doneButton .icon{

}

.spacer{

    background-color: #212A41;

    background-color: var(--background1);
    color: #F6F7F8;
    color: var(--foreground);
}

/* Holder with Modul */
.modulHolder{

    color: #F6F7F8;

    color: var(--foreground);
}





.footerWrapper{

    display: flex;
    flex-direction: column;
    grid-row-gap: 0.25rem;
    row-gap: 0.25rem;
    grid-row-gap: var(--holder-gap);
    row-gap: var(--holder-gap);
    width: 78.5%;
    width: var(--size-relation);
}



.statusWrapper{

    display: flex;
    background-color: #212A41;
    background-color: var(--background1);
    height: 60%;
}

.statusText{

    font-size: 2.17rem;

    font-size: var(--statusText-size);
    color: #F6F7F8;
    color: var(--foreground);
    margin: auto;
    margin-left: 1rem;
}
.statusText.active{

    -webkit-animation: blendInText linear 0.7s 2.3s;

            animation: blendInText linear 0.7s 2.3s;
}
@-webkit-keyframes blendInText {
    
    from{
        opacity: 100%;
    }
    to{
        opacity: 0%;
    }
}
@keyframes blendInText {
    
    from{
        opacity: 100%;
    }
    to{
        opacity: 0%;
    }
}

.lpWrapper{

    display: flex;
    background-color: #212A41;
    background-color: var(--background1);
    height: 40%;
}

.lpText{

    color: #F6F7F8;

    color: var(--foreground);
    margin: auto;
    margin-left: 1rem;
    font-size: 1.37rem;
    font-size: var(--lpText-size);
}



.overviewWrapper{

    display: flex;
    width: calc(calc(100% - 78.5% - 4 * 0.25rem) + 0.25rem);
    width: calc(var(--size-relation-invert) + var(--holder-gap));
    background-color: #212A41;
    background-color: var(--background1);
}





/* Popup */
.modal .modal-content{

    background-color: #212A41;

    background-color: var(--background1);
    color: #F6F7F8;
    color: var(--foreground);
}

.modal.options{

    max-height: 60vh;
}

/* Options Container */
.optionsContainer{

    display: flex;
    background-color: #1A1D25;
    background-color: var(--background2);
    border: 1px solid #1A1D25;
    border: 1px solid var(--background2);
    color: #F6F7F8;
    color: var(--foreground);
    width: 100%;
    -webkit-user-select: none;
            user-select: none;
    outline: none;
    height: 600px;
    overflow: scroll;
}




/* width */
::-webkit-scrollbar {
    
    width: 20px;
    
    width: var(--scrollbar-size);
    height: 20px;
    height: var(--scrollbar-size);
}
  
/* Track */
::-webkit-scrollbar-track {
    
    
}
  
/* Handle */
::-webkit-scrollbar-thumb {
    background: #212A41;
    background: var(--background1);

}

::-webkit-scrollbar-corner{


}
